body {
  margin: 0;
  padding: 0;
  background: #F2F4F5;
  color: #1E1E1F;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}
.ToDoCounter{
    font-size: 24px;
    text-align: center;
    margin: 0;
    padding: 48px;
}
.ToDoSearch{
    background: #F9FBFC;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.ToDoSearch::-webkit-input-placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.ToDoSearch:-ms-input-placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.ToDoSearch::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.ToDoSearch:focus {
  outline-color: #61DAFA;
}
.ToDoList{
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
}
.ToDoItem{
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.ToDoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.ToDoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
}

.Icon-check {
    position: absolute;
    left: 12px;
}

/*
.Icon-check--active {
    color: #4caf50;
}
*/

.Icon-delete {
    position: absolute;
    top: -5px;
    right: 0px;
}

/*
.Icon-delete:hover {
    color: red;
}
*/
.CreateToDoButton {
  background-color: rgb(10,132,255);
    /*background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);*/
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
  
    -webkit-transform: rotate(0);
  
            transform: rotate(0);
    transition: .3s ease;
    z-index: 1;
  }
  
  .CreateToDoButton:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
  }
.ModalBackground{
    background:rgba(32,35,41,.8);
    position:fixed;
    top:-10px;
    left:-10px;
    right:-10px;
    bottom:-10px;
    display:flex;
    justify-content:center;
    align-items:center;
    color:#fff
}
form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    align-content: center;
    flex-direction: column;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: #1E1E1F;
    margin-bottom: 26px;
  }
  
  textarea {
    background-color: #F9FBFC;
    border: 2px, solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100%-25px);
    resize: vertical;
  }
  
  textarea::-webkit-input-placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
  }
  
  textarea:-ms-input-placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
  }
  
  textarea::placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #fff;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button--add {
    background: rgb(50,215,75);
    /*
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    */
  }
  
  .TodoForm-button--cancel {
    background: rgb(255,69,58);
    /*box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);*/
  }
.container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
}

.loader {
    border: 10px solid #f3f3f3;
    border-top: 10px solid #3498db;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;
  }

  @-webkit-keyframes spin {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

  @keyframes spin {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
.containerEmpty{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    box-shadow: 4px 8px 12px 0 rgba(0,0,0,0.4);
}

.boxEmpty{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0px;
    width: calc(100% - 120px);
    
}

.boxEmpty p{
    flex-grow: 1;
    color:rgb(28,28,20);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.isLoading{
    justify-content: flex-end;
    margin: 24px 0px;
    width: calc(100% - 120px);
    height: 100px;
    cursor: progress;
    -webkit-animation: 1.5s loadingAnimation linear infinite;
            animation: 1.5s loadingAnimation linear infinite;
    background: #eee;
    background: linear-gradient(
            110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%
        );
    background-size: 200% 100%;
}

@-webkit-keyframes loadingAnimation {
    to {
        background-position-x: -200%;
    }
}

@keyframes loadingAnimation {
    to {
        background-position-x: -200%;
    }
}
.containerError{
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    box-shadow: 1px 2px 10px 0px rgba(0,0,0,0.3);
}

.containerError:hover {
    box-shadow: 4px 8px 12px 0 rgba(0,0,0,0.4);
}

.textError{
    width: 100%;
    height: 40px;
    margin: 0px 0px;
    text-align: center;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    color: #fff;
    background: #F65656;
}

.textError:hover{
    background: #EC3434;
}

.imgError{
    width: 100%;
    text-align: center;
    padding: 20px 0px;
}
